import { Container, Row, Col } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import "animate.css";
// import logo from "../assets/img/logos/programmingLogos/git-original.svg"
import cvSite from "../assets/img/cv-site-proj.png";
// import pongIcon from "../assets/img/ping-pong.svg";
import qdIcon from "../assets/img/logos/42_Logo.min.svg";
// import cvSiteMaker from "../assets/img/cv-site-maker-proj.png";
import termImg from "../assets/img/terminal.svg";
import containersImg from "../assets/img/linuxContainers.svg";
import chatImg from "../assets/img/chat.svg";

export const Projects = () => {
  const projects = [
    {
      title: "Cv site",
      description: "My curriculum vitae web version",
      imgUrl: cvSite,
      url: "https://nnieddu.github.io/cv/index.html",
    },
    {
      title: "ft_transcendence",
      description: "Final 42 Lyon school project, a multiplayer Pong game webapp (SPA)",
      imgUrl: qdIcon,
      url: "https://github.com/M-Philippe/ft_transcendence",
    },
    // {
    //   title: "SiteMaker",
    //   description: "Little interface to create website (WIP)",
    //   imgUrl: cvSiteMaker,
    //   url: "https://nnieddu.github.io/cvm/",
    //   style: "0 35px"
    // },
    {
      title: "minishell",
      description: "Reimplementation of a command interpreter in C",
      imgUrl: termImg,
      url: "https://github.com/nnieddu/minishell",
    },
    {
      title: "ft_containers ",
      description: "Implementation of some STL containers (vector, map, stack) in C++",
      imgUrl: containersImg,
      url: "https://github.com/nnieddu/ft_containers",
    },
    {
      title: "ft_irc",
      description: "Serveur IRC (RFC 1459, 2813 compliant) in C++",
      imgUrl: chatImg,
      url: "https://github.com/nnieddu/ft_irc",
    },
  ];

  return (
    <Container id="projects" className="contentSection">
      <Row>
        <Col size={12}>
          <h2>Projects</h2>
          <p>Some of my projects</p>
          <Row>
            {projects.map((project, index) => {
              return <ProjectCard key={index} {...project} />;
            })}
          </Row>
        </Col>
      </Row>
    </Container>
  );
};
