import "./styles/App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { NavBar } from "./components/NavBar";
import { Home } from "./components/Home";
import { Projects } from "./components/Projects";
import { Skills } from "./components/Skills";
import { Bonus } from "./components/Bonus";
import { Footer } from "./components/Footer";
import warpspeed from "./assets/utils/warpspeed.js";
import { useEffect } from "react";
import Earth from "./components/Planet";

function App() {
  useEffect(() => {
    new warpspeed("starsW-bg");
  }, []);

  return (
    <div className="App">
      <NavBar />
      <Home />
      <Earth/>
      <Projects />
      <Skills />
      <Bonus />
        <h2 style={{margin: "0 5px 100px 5px", textAlign: "center"}}>Thank you for your attention</h2>
      <Footer />
      <div id="stars-bg-div">
        <canvas id="starsW-bg"></canvas>
      </div>
    </div>
  );
}

export default App;
