import { Container, Row, Col } from "react-bootstrap";
import "animate.css";
import TypeWriter from "./Typewriter";
// import pp from "../assets/img/pp.webp";
import pp from "../assets/img/pp.jpg";

export const Home = () => {
  const typeWriter = ["Hi ! I'm Nicolas, a developer."];
  const today = new Date();
  const birthDate = new Date("1994/06/06");
  var age = today.getFullYear() - birthDate.getFullYear();
  const month = today.getMonth() - birthDate.getMonth();

  if (month < 0 || (month === 0 && today.getDate() < birthDate.getDate())) {
    age--;
  }

  return (
    <Container id="home">
      <Col size={12}>
        <Row>
          <Col size={12}>
            <h1 className="animate__animated animate__slideInLeft">
              <br />
              <div className="hiMsg">
                <TypeWriter data={typeWriter} />
                <span className="typewriter"></span>
              </div>
              <div id="profilPic" >
                <img src={pp} alt="Nicolas portrait" />
              </div>
            </h1>
          </Col>
        </Row>
      </Col>
      <Row>
        <p className="animate__animated animate__slideInRight">
          My name's Nicolas NIEDDU, I'm {age} years old and I'm from Lyon in France.
          <br />
          <br />
          I'm in love with computer science, videos games, hacking.. since I was a child,
          that's why at the end of 2019 I join the 42 school to mix work and passion.
          <br />
          <br />
          I'm junior developer, I finished the 42 lyon school common core which brought me
          a lot in term of scociability and skills.
          <br />
          <br />
          So for now I'm working on a website maker UI and an online browser game but{" "}
          <strong>I especially looking for a front-end, ReactJS developer job.</strong>
        </p>
      </Row>
    </Container>
  );
};
