import { Container, Row, Col } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";

import "animate.css";
import cLogo from "../assets/img/logos/programmingLogos/c-original.svg";
import cppLogo from "../assets/img/logos/programmingLogos/cplusplus-original.svg";
import htmlLogo from "../assets/img/logos/programmingLogos/html5-original.svg";
import cssLogo from "../assets/img/logos/programmingLogos/css3-original.svg";
import sassLogo from "../assets/img/logos/programmingLogos/sass-original.svg";
import jsLogo from "../assets/img/logos/programmingLogos/javascript-original.svg";
import tsLogo from "../assets/img/logos/programmingLogos/typescript-original.svg";
import reactLogo from "../assets/img/logos/programmingLogos/react-original.svg";
import dockerLogo from "../assets/img/logos/programmingLogos/docker-original.svg";
import githubLogo from "../assets/img/logos/programmingLogos/github-original.svg";
import psLogo from "../assets/img/logos/programmingLogos/photoshop-line.svg"
// import jestLogo from "../assets/img/logos/programmingLogos/jest-plain.svg";
export const Skills = () => {
  const skills = [
    {
      title: "C",
      description: " ",
      imgUrl: cLogo,
    },
    {
      title: "C++",
      description: " ",
      imgUrl: cppLogo,
    },
    {
      title: "HTML",
      description: " ",
      imgUrl: htmlLogo,
    },
    {
      title: "CSS",
      description: " ",
      imgUrl: cssLogo,
    },
    {
      title: "SASS",
      description: "SCSS / SASS",
      imgUrl: sassLogo,
    },
    {
      title: "Javascript",
      description: " ",
      imgUrl: jsLogo,
    },
    {
      title: "Typescript",
      description: " ",
      imgUrl: tsLogo,
    },
    {
      title: "React JS",
      description: "+ Redux / Redux toolkit",
      imgUrl: reactLogo,
    },
    {
      title: "Docker",
      description: " ",
      imgUrl: dockerLogo,
    },
    {
      title: "Git / GitHub",
      description: " ",
      imgUrl: githubLogo,
    },
    {
      title: "Photoshop",
      description: " ",
      imgUrl: psLogo,
    },
    // {
    //   title: "Jest",
    //   description: " ",
    //   imgUrl: jestLogo,
    // },
  ];

  return (
      <Container id="skills" className="contentSection">
        <Row>
          <Col size={12}>
                  <h2>Skills</h2>
                  <br/>
                  <Row>
                    {skills.map((project, index) => {
                      return <ProjectCard key={index} {...project} />;
                    })}
                  </Row>
          </Col>
        </Row>
      </Container>
  );
};
