import { useState, useEffect } from "react";
import { Navbar, Nav } from "react-bootstrap";
import logo from "../assets/img/logos/logoNN.svg";
import linkedinIcon from "../assets/img/logos/linkedin-icon.svg";
import githubIcon from "../assets/img/logos/github-icon.svg";
import linkedinIconLight from "../assets/img/logos/light/linkedin-icon-light.svg";
import discordIconLight from "../assets/img/logos/light/discord-icon-light.svg";
import discordIcon from "../assets/img/logos/discord-icon.svg";
import { BrowserRouter as Router } from "react-router-dom";
import "animate.css";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
// import flagIconUS from "../assets/img/flags/us.svg";
// import flagIconFR from "../assets/img/flags/fr.svg";

const openInNewTab = (url) => {
  window.open(url, "_blank", "noopener,noreferrer");
};

export const NavBar = () => {
  const [activeLink, setActiveLink] = useState("home");
  const [scrolled, setScrolled] = useState(false);
  const [modalShow, setModalShow] = useState(false);
  const [navBarCollapse, setNavBarCollapse] = useState(false);
  const [navBarExpand, setNavBarExpand] = useState(false);

  function scrollToTopic(value) {
    const id = value;
    const yOffset = -125;
    const element = document.getElementById(id);
    const y = element.getBoundingClientRect().top + window.pageYOffset + yOffset;
    window.scrollTo({ top: y, behavior: "smooth" });
  }

  // const containers = document.getElementsByClassName("container");
  // const navLi = document.getElementsByClassName("navbar-link");

  useEffect(() => {
    const onScroll = (e) => {
      setNavBarExpand(false);
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }

    //   var current = "";
    //   for (let i = 0; i < containers.length; i++) {
    //     const sectionTop = containers[i].offsetTop;
    //     // console.log("WPY: " + window.pageYOffset);
    //     // console.log("SEC: " + sectionTop);
    //     if (window.pageYOffset > sectionTop - 40) {
    //       current = containers[i].id;
    //       for (let i = 0; i < navLi.length; i++) {
    //         navLi[i].classList.remove("active");
    //         if (navLi[i].classList.contains(current)) 
    //         {
    //           setActiveLink(current);
    //           navLi[i].classList.add("active");
    //         }
    //       }
    //     }
    //   }
    };


    // var getElementsInArea = (function(docElm){
    //   var viewportHeight = docElm.clientHeight;
    
    //   return function(e, opts){
    //       var found = [], i;
          
    //       if( e && e.type === 'resize' )
    //           viewportHeight = docElm.clientHeight;
    
    //       for( i = opts.elements.length; i--; ){
    //           var elm        = opts.elements[i],
    //               pos        = elm.getBoundingClientRect(),
    //               topPerc    = pos.top    / viewportHeight * 100,
    //               bottomPerc = pos.bottom / viewportHeight * 100,
    //               middle     = (topPerc + bottomPerc)/2,
    //               inViewport = middle > opts.zone[1] && 
    //                            middle < (100-opts.zone[1]);
    
    //           elm.classList.toggle(opts.markedClass, inViewport);
    
    //           if( inViewport )
    //               found.push(elm);
    //       }
    //   };
    // })(document.documentElement);
    
    // function f(e){
    //  console.log(getElementsInArea(e, {
    //       elements    : document.querySelectorAll('div'), 
    //       markedClass : 'active',
    //       zone        : [20, 20] // percentage distance from top & bottom
    //   }));
    // }
    
    function handleClickOutside(e) {
      if (
        e.target &&
        !e.target.className.includes("navbar") &&
        e.target.id !== "rightButtonsDiv" &&
        !e.target.innerHTML.includes("Contact me")
      )
        setNavBarExpand(false);
    }
    document.addEventListener("mousedown", handleClickOutside);
    window.addEventListener("scroll", onScroll);
    return () => {
      window.removeEventListener("mousedown", handleClickOutside);
      window.removeEventListener("scroll", onScroll);
    };
  }, );

  const onUpdateActiveLink = (value) => {
    setActiveLink(value);
  };

  function MyVerticallyCenteredModal(props) {
    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton></Modal.Header>
        <Modal.Body style={{ textAlign: "center" }}>
          <br />
          <a
            style={{
              color: "black",
              textDecoration: "none",
              border: "solid 2px black",
              borderRadius: "5px",
              padding: "10px 10px",
            }}
            href="mailto:nicolas.nieddu@free.fr"
          >
            nicolas.nieddu@free.fr
          </a>
          <br />
          <div style={{ color:"black" }}>
            <a href="https://discord.gg/yz7FEtzx" style={{ margin: "10px" }}>
              <img src={discordIconLight} alt="Profil discord de Nicolas NIEDDU" />
            </a>
            <a
              style={{ margin: "10px" }}
              href="https://www.linkedin.com/in/nicolas-nieddu-71406b192/"
            >
              <img src={linkedinIconLight} alt="Profil linkedin de Nicolas NIEDDU" />
            </a>
            <br />
            <span>My CV :&nbsp;&nbsp;</span>
            <a style={{ margin: "10px", textDecoration: "none", color:"black" }} href="https://nnieddu.github.io/cv/index.html">
              Web version
            </a>
            <a style={{ margin: "10px",textDecoration: "none", color:"black" }} href="https://nnieddu.github.io/cv.pdf">
              PDF
            </a>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="dark" onClick={props.onHide}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
  return (
    <Router>
      <Navbar expand="md" className={scrolled ? "scrolled" : ""} expanded={navBarExpand}>
        <Navbar.Toggle
          onClick={() => {
            setNavBarCollapse(true);
            setNavBarExpand(!navBarExpand);
          }}
        >
          <span className="navbar-toggler-icon"></span>
        </Navbar.Toggle>
        <Navbar.Collapse className="navbar-text">
          {!navBarCollapse && <img id="logoNN" src={logo} alt="Logo Nicolas NIEDDU" />}
          <Nav
            style={{
              margin: navBarCollapse ? "0 0 0px -10px" : "0 10px",
              padding: navBarCollapse ? "0px 0 10px 0" : "0 10px",
            }}
          >
            <Nav.Link
              className={
                activeLink === "home" ? "active navbar-link" : "home navbar-link"
              }
              onClick={() => {
                onUpdateActiveLink("home");
                scrollToTopic("home");
              }}
            >
              Home
            </Nav.Link>
            <Nav.Link
              className={
                activeLink === "projects" ? "active navbar-link" : "projects navbar-link"
              }
              onClick={() => {
                onUpdateActiveLink("projects");
                scrollToTopic("projects");
              }}
            >
              Projects
            </Nav.Link>
            <Nav.Link
              className={
                activeLink === "skills" ? "active navbar-link" : "skills navbar-link"
              }
              onClick={() => {
                onUpdateActiveLink("skills");
                scrollToTopic("skills");
              }}
            >
              Skills
            </Nav.Link>
          </Nav>
          <div id="rightButtonsDiv">
            <span className="social-icon">
              <p onClick={() => openInNewTab("https://github.com/nnieddu")}>
                <img src={githubIcon} alt="Profil Github de Nicolas NIEDDU" />
              </p>
              <p onClick={() => openInNewTab("https://discord.gg/yz7FEtzx")}>
                <img src={discordIcon} alt="Profil discord de Nicolas NIEDDU" />
              </p>
              <p
                onClick={() =>
                  openInNewTab("https://www.linkedin.com/in/nicolas-nieddu-71406b192/")
                }
              >
                <img src={linkedinIcon} alt="Profil linkedin de Nicolas NIEDDU" />
              </p>
            </span>
            <span>
              <button onClick={() => setModalShow(true)} className="contactMeBtn">
                <span>Contact me</span>
              </button>
              <MyVerticallyCenteredModal
                show={modalShow}
                onHide={() => setModalShow(false)}
              />
            </span>
            {/* <span className="flag-icon">
              <a href="/">
                <img src={flagIconUS} alt="Language flag icon" />
              </a>
            </span> */}
          </div>
        </Navbar.Collapse>
      </Navbar>
    </Router>
  );
};
