import { Col } from "react-bootstrap";

const openInNewTab = url => {
  window.open(url, '_blank', 'noopener,noreferrer');
};

export const ProjectCard = ({ title, description, imgUrl, url, style }) => {
  return (
    <Col size={12} sm={6} md={4}>
      <div onClick={() => openInNewTab(url)} className="card-proj-div animate__animated animate__rollIn">
        <img className="card-proj-img" src={imgUrl} alt="project visual" />
        <div className="card-proj-txt">
          <h4>{title}</h4>
          <span>{description}</span>
        </div>
      </div>
    </Col>
  )
}
