import { Container, Row, Col } from "react-bootstrap";
import logo from "../assets/img/logos/logoNN.svg";
import arrowUp from "../assets/img/arrow-up.svg";
import linkedinIcon from "../assets/img/logos/linkedin-icon.svg";
import githubIcon from "../assets/img/logos/github-icon.svg";
import discordIcon from "../assets/img/logos/discord-icon.svg";
import { useState, useEffect } from "react";

const openInNewTab = url => {
  window.open(url, '_blank', 'noopener,noreferrer');
};

function scrollToTopArrow() {
  document.body.scrollTop = 0;
  document.documentElement.scrollTop = 0;
}

export const Footer = () => {
  const [isActive, setActive] = useState(true);

  useEffect(() => {
    const onResize = () => {
      if (window.pageYOffset + window.innerHeight > document.body.scrollHeight)
        setActive(false);
      else setActive(true);
    };
    onResize();
    window.addEventListener("resize", onResize);
    return () => window.removeEventListener("resize", onResize);
  }, []);

  return (
    <Container className={isActive ? "footer" : "footer fixed-footer"}>
      <Row>
        <Col sm>
          <img className="logoNN-footer" src={logo} alt="Logo Nnieddu"/>
        </Col>
        <Col onClick={scrollToTopArrow} id="arrow-up-btn" sm>
          <img id="arrow-up-btn-img" src={arrowUp} alt="Go up arrow button" />
        </Col>
        <Col sm>
          <div className="social-icon">
              <p
              onClick={() => openInNewTab("https://github.com/nnieddu")}>
                <img src={githubIcon} alt="Profil Github de Nicolas NIEDDU" />
              </p>
              <p
              onClick={() => openInNewTab("https://discord.gg/yz7FEtzx")}>
                <img src={discordIcon} alt="Profil discord de Nicolas NIEDDU" />
              </p>
              <p
              onClick={() => openInNewTab("https://www.linkedin.com/in/nicolas-nieddu-71406b192/")}>
                <img src={linkedinIcon} alt="Profil linkedin de Nicolas NIEDDU" />
              </p>
          </div>
        </Col>
      </Row>
    </Container>
  );
};
